const seasonDefault = {
  season1: {
    color: "rgb(255, 0, 255)",
    colorArray: [255.0 / 255.0, 0.0, 255.0 / 255.0],
    foodConsumption: 0.2,
    population: 0.2,
    temperature: 0.2
  },
  season2: {
    color: "rgb(0, 255, 255)",
    colorArray: [0.0, 255.0 / 255.0, 255.0 / 255.0],
    foodConsumption: 0.4,
    population: 0.4,
    temperature: 0.4
  },
  season3: {
    color: "rgb(0, 0, 255)",
    colorArray: [0.0, 0.0, 255.0 / 255.0],
    foodConsumption: 0.6,
    population: 0.6,
    temperature: 0.6
  },
  season4: {
    color: "rgb(198, 156, 109)",
    colorArray: [198.0 / 255.0, 156.0 / 255.0, 109 / 255.0],
    foodConsumption: 0.8,
    population: 0.8,
    temperature: 0.8
  }
};

export default class Store {
  constructor() {
    this.activeSeason = "1";
    this.seasons = seasonDefault;
    this.background = "rgb(0, 0, 0)";
    this.backgroundArray = [0.0, 0.0, 0.0];
    this.seasonsLength = Object.keys(seasonDefault).length
  }

  updateActiveSeason = season => {
    this.activeSeason = season;
  };

  updateSlider = (key, value) => {
    this.seasons[`season${this.activeSeason}`][key] = value;
  };

  updateColor = (value, rgb) => {
    this.seasons[`season${this.activeSeason}`].color = value;
    this.seasons[`season${this.activeSeason}`].colorArray = normalizeColor(rgb);
  };

  updateBackground = (value, rgb) => {
    this.background = value;
    this.backgroundArray = normalizeColor(rgb);
  };

  // getFoodConsumption = () => {
  //   return this.seasons[`season${this.activeSeason}`].foodConsumption;
  // };

  // getPopulation = () => {
  //   return this.seasons[`season${this.activeSeason}`].population;
  // };

  // getTemperature = () => {
  //   return this.seasons[`season${this.activeSeason}`].temperature;
  // };

  getSliderValue = (type, season) => {
    return this.seasons[`season${season ? season : this.activeSeason}`][type];
  };

  getColor = (season) => {
    return this.seasons[`season${season}`].colorArray;
  }

  getBackground = () => {
    return this.backgroundArray
  }
}

const normalizeColor = ({r, g, b}) => {
  return [r / 255.0, g / 255.0, b / 255.0]
}