import Store from "./js/store";
import GL from "./js/gl";
import linesImagePath from "./images/lines.png";
import lettersImagePath from "./images/letters.png";

import { ColorPicker, SeasonButton, Slider } from "./js/controls";

window.addEventListener("load", async () => {
  const lettersImage = await loadImage(lettersImagePath);
  const linesImage = await loadImage(linesImagePath);

  const canvas = document.getElementById("canvas");
  const footer = document.getElementById("footer");
  const dlBtn = document.getElementById("download");
  const seasons = {};
  const sliders = {};

  const globalStore = new Store();
  const gl = new GL(canvas, globalStore, lettersImage, linesImage);

  canvas.width = window.innerWidth;
  canvas.height = window.innerHeight - footer.offsetHeight;

  // Initialize in DOM element to Javascript
  const resizeView = () => {
    canvas.style.transform = `matrix(${window.innerWidth /
      canvas.width}, 0, 0, ${(window.innerHeight - footer.offsetHeight) /
      canvas.height}, ${(window.innerWidth - canvas.width) /
      2}, ${(window.innerHeight - footer.offsetHeight - canvas.height) / 2})`;
  };

  window.addEventListener("resize", resizeView, false);
  window.addEventListener("orientationchange", resizeView, false);
  resizeView();

  dlBtn.addEventListener("click", evt => {
    const image = canvas.toDataURL("image/png");
    dlBtn.href = image;
  });

  for (let i = 1; i < 4; i++) {
    sliders[`slider${i}`] = new Slider(i, globalStore);
  }

  for (let i = 1; i < 5; i++) {
    seasons[`season${i}`] = new SeasonButton(i, globalStore, sliders, seasons);
  }

  new ColorPicker("season-color", globalStore, seasons);
  new ColorPicker("screen-color", globalStore);

  gl.initialize();
});

const loadImage = url => {
  return new Promise(function(resolve) {
    var image = new Image();
    image.src = url;
    image.onload = function() {
      resolve(image);
    };
  });
};
